import { useEffect, useState } from 'react';
import './App.css';

function App() {
  const [task, setTask] = useState('');
  const [taskList, setTaskList] = useState([]);

  const handleChange = (e) => {
    const text = e.target.value;
    setTask(text);
  }
  const handleSave = () => {
    let taskObj = {};
    taskObj["Text"] = task;
    taskObj["Status"] = false;
    taskObj["Id"] = Date.now();
    saveTask(taskObj)
  }
  const saveTask = (taskObj) => {
    let tempList = taskList
    tempList.push(taskObj)
    localStorage.setItem("todoList", JSON.stringify(tempList))
    setTaskList(tempList)
    window.location.pathname = '/react-todoapp/'
  }
  useEffect(() => {
    let arr = localStorage.getItem("todoList");
    if (arr) {
      let obj = JSON.parse(arr);
      setTaskList(obj)
    }
  }, [])
  const handleDelete = (index) => {
    let tempList = taskList
    tempList.splice(index, 1)
    localStorage.setItem("todoList", JSON.stringify(tempList))
    setTaskList(tempList)
    window.location.pathname = '/react-todoapp/'
  }
  return (
    <div className="app">
      <div className="mainHeading">
        <h1>ToDo List</h1>
      </div>
      <div className="Toleft">
        <div className="input">
          <input type="text" onChange={(e) => handleChange(e)} id="input" placeholder="🖊️ Add item..." className="mr" />
          <i onClick={() => {
            if (document.getElementById("input").value) {
              handleSave()
            }
          }}>+</i>
        </div>
        {taskList.map((obj, index) => {
          return (
            <div className="todos">
              <div className="todo">
                <div className="left">
                  <input type="checkbox" onChange={(e) => {
                    setTaskList(taskList.filter(obj2 => {
                      if (obj2.Id === obj.Id) {
                        obj2.Status = e.target.checked
                        localStorage.setItem("todoList", JSON.stringify(taskList));
                      }
                      return obj2
                    }))
                  }} checked={obj.Status} className="mr" />
                  {obj.Status ? <p className="p"><del>{obj.Text}</del></p> : <p className="p">{obj.Text}</p>}
                </div>
                <div key={index} className="right">
                  <i onClick={() => handleDelete(index)}>×</i>
                </div>
              </div>
            </div>)
        })}
      </div>
    </div>
  )
}

export default App
