import React from 'react'
import './Bottom.css'

function Bottom() {
    function handleShare() {
        navigator.share({
            title: 'Todo',
            text: 'Open the link and click the add to home screen banner',
            url: 'https://anagh534.github.io/react-todoapp/',
        })
    }
    return (
        <div>
            <nav className="nav">
                <button onClick={() => window.location.pathname = "/react-todoapp/"} className="nav__link">
                    <i className="material-icons nav__icon">
                        home
                    </i>
                </button>
                <button className="nav__link" onClick={() => alert("wait for next version..")}>
                    <i className="material-icons nav__icon">
                        format_list_bulleted
                    </i>
                </button>
                <button onClick={() => window.location.pathname = "/react-todoapp/offline.html"} className="nav__link">
                    <span className="material-icons nav__icon">
                        sports_esports
                    </span>
                </button>
                <button onClick={() => handleShare()} className="nav__link">
                    <i className="material-icons nav__icon">
                        share
                    </i>
                </button>
            </nav>
        </div>
    )
}

export default Bottom
